.work {

    .info-section {
        width: 20%;
    }
    .content-area {
        margin-left: 20%;
        width: 80%;
        min-width: 300px;
        background: white;
        height: 100%;
        overflow: hidden;
        padding: 56px;
        
        .work-section {
            margin-bottom: 56px;

            h1 {
                font-weight: 700;
                font-size: 2rem;
                text-transform: uppercase;
                margin-bottom: 56px;
            }

            .project-section {
                display: grid;
                grid-template-columns: 1fr 1fr;
                gap: 10px;

                .project-box {
                    position: relative;
                    padding: 30px;
                    transition: all 0.5s ease-in-out;
                    // cursor: pointer;
                    -webkit-background-size: cover;
                    -moz-background-size: cover;
                    -o-background-size: cover;
                    background-size: cover; 
                    height: 400px;

                    &:hover {
                        box-shadow: 0 20px 74px 6px rgba(0,0,0,0.12);
                    }

                    .grey-bg {
                        // background: $color4;
                        background: #efefef;
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        right: 0;
                        left: 0;
                        opacity: 0;
                        transition: all 0.3s ease-in-out;

                        &:hover {
                            opacity: 0.9;
                        }

                        .project-info {
                            padding: 56px;
                            position: absolute;
                            bottom: 0;
                            .project-details {
                                font-size: 1.1rem;
                                text-transform: capitalize;
                            }
                            .project-title {
                                font-size: 1.8rem;
                                text-transform: uppercase;
                                font-weight: 700;
                            }
                        }
                    }

                    h5 {
                        font-weight: 500;
                    }
                }
            }
        }
    }
}